import { getStartTouch, blockEvent } from './utils';

type IDraggerConstructorParams = {
  getDrawer: () => (HTMLDivElement | null);
  getBg: () => (HTMLDivElement | null);
  getDraggable: () => (HTMLDivElement | null);
  getRoot: () => (HTMLDivElement | null);
}

const SCROLL_THRESHOLD = 5;
type ScrollDirection = 'horizontal' | 'vertical' | 'notScroll';
type Status = 'scrolling' | 'dragging' | 'none';

export class Dragger {
  constructor(params: IDraggerConstructorParams) {
    this.getDrawer = params.getDrawer;
    this.getBg = params.getBg;
    this.getDraggable = params.getDraggable;
    this.getRoot = params.getRoot;
  }
  private startTouch: Touch | null = null;
  private translateY: number = 0;
  private drawerHeight: number = 0;
  private getDrawer: () => HTMLDivElement | null;
  private getBg: () => HTMLDivElement | null;
  private getDraggable: () => (HTMLDivElement | null);
  private getRoot: () => (HTMLDivElement | null);
  private scrollDirection: ScrollDirection = 'notScroll';
  private status: Status = 'none';

  onTouchStart = (e: TouchEvent) => {
    e.stopPropagation();
    const { changedTouches } = e;
    const { length } = changedTouches;
    if (length > 1) {
      return;
    }
    this.startTouch = changedTouches[0];
    this.translateY = 0;
    this.getRoot()?.addEventListener('touchmove', this.onTouchMove, {
      passive: false,
    });
    this.getRoot()?.addEventListener('touchend', this.onTouchEnd);
  }

  touchEnd = (curTranslateY?: number) => {
    const drawer = this.getDrawer();
    const bg = this.getBg();
    if (!drawer || !bg || !(this.status === 'dragging')) {
      return;
    }
    const realTranslateY = curTranslateY || this.translateY;
    const height = this.drawerHeight || drawer?.clientHeight || 0;

    if (realTranslateY > height * 0.25) {
      drawer.style.transform = `translateY(${height}px)`;
      drawer.style.transition = 'transform 0.2s';
      bg.style.opacity = '0';
      bg.style.transition = 'opacity 0.6s';
      this.translateY = height;
    } else {
      drawer.style.transform = 'translateY(0px)';
      drawer.style.transition = 'transform 0.2s';
      bg.style.opacity = '1';
      bg.style.transition = 'none';
      this.translateY = 0;
    }
    this.startTouch = null;
  }

  formatTransformY = (transformY: number) => {
    const maxTransformY = this.drawerHeight || this.getDrawer()?.clientHeight || 0;
    return Math.min(maxTransformY, Math.max(0, transformY));
  };

  isScrollable = (target: HTMLElement | null, direction: 'horizontal' | 'vertical', delta: number): boolean => {
    if (!target) return false;
    const root = this.getRoot();
    if (root === target) {
      return false;
    }
    if (direction === 'horizontal') {
      const { scrollWidth, clientWidth, scrollLeft } = target;
      if (scrollWidth > clientWidth) {
        const {overflowX, overflow} = getComputedStyle(target);
        if (overflowX === 'scroll' || overflowX === 'auto' || overflow === 'scroll' || overflow === 'auto') {
          if ((delta > 0 && scrollLeft > 0) || (delta < 0 && target.scrollTop + clientWidth < scrollWidth)) {
            return true;
          }
        }
      }
    } else {
      const { scrollHeight, clientHeight, scrollTop } = target;
      if (scrollHeight > clientHeight) {
        const {overflowY, overflow} = getComputedStyle(target);
        if (overflowY === 'scroll' || overflowY === 'auto' || overflow === 'scroll' || overflow === 'auto') {
          if ((delta > 0 && scrollTop > 0) || (delta < 0 && target.scrollTop + clientHeight < scrollHeight)) {
            return true;
          }
        }
      }
    }
    return this.isScrollable(target.parentElement, direction, delta);
  }

  onTouchMove = (e: TouchEvent) => {
    const { changedTouches } = e;
    const touch = getStartTouch(changedTouches, this.startTouch?.identifier);
    if (!touch) {
      blockEvent(e);
      this.touchEnd();
    } else {
      const { clientY, clientX } = touch;
      const { target, clientX: startTouchX, clientY: startTouchY } =  this.startTouch!;
      const moveX = clientX - startTouchX;
      const moveY = clientY - startTouchY;
      if (this.status === 'none' && Math.max(Math.abs(moveX), Math.abs(moveY)) > SCROLL_THRESHOLD) {
        const direction: ScrollDirection = Math.abs(moveX) > Math.abs(moveY) ? 'horizontal' : 'vertical';
        const delta = direction === 'horizontal' ? moveX : moveY;
        const canScroll = this.isScrollable(target as HTMLElement, direction, delta);
        this.scrollDirection = direction;
        if (canScroll) {
          this.status = 'scrolling';
          e.stopPropagation();
        } else if (direction === 'vertical') {
          this.status = 'dragging';
          blockEvent(e);
        } else {
          blockEvent(e);
        }
      } else if (this.status === 'scrolling') {
        e.stopPropagation();
      } else if (this.status === 'dragging') {
        blockEvent(e);
        this.translateY = this.formatTransformY(moveY);
        const drawer = this.getDrawer();
        if (drawer) {
          drawer.style.transform = `translateY(${this.translateY}px)`;
          drawer.style.transition = 'none';
        }
      } else {
        blockEvent(e);
      }
    }
  }

  onTouchEnd = (e: TouchEvent) => {
    e.stopPropagation();
    const { changedTouches } = e;
    const touch = getStartTouch(changedTouches, this.startTouch?.identifier);
    if (!touch) {
      this.touchEnd();
    } else {
      this.translateY = this.formatTransformY(touch.clientY - (this.startTouch as Touch).clientY);
      this.touchEnd(this.translateY);
    }
    this.getRoot()?.removeEventListener('touchmove', this.onTouchMove);
    this.getRoot()?.removeEventListener('touchend', this.onTouchEnd);
    this.scrollDirection = 'notScroll';
    this.status = 'none';
  }

  slideOut = () => {
    const drawer = this.getDrawer();
    const bg = this.getBg();
    if (!drawer || !bg) {
      return;
    }
    const newTranslateY = this.drawerHeight || drawer.clientHeight || 0;
    drawer.style.transform = `translateY(${newTranslateY}px)`;
    drawer.style.transition = 'transform 0.4s';
    bg.style.opacity = '0';
    bg.style.transition = 'opacity 0.4s';
  };

  slideIn = () => {
    const drawer = this.getDrawer();
    const bg = this.getBg();
    if (!drawer || !bg) {
      return;
    }
    this.drawerHeight = drawer.clientHeight || 0;
    bg.style.transition = 'opacity 0.2s';
    bg.style.opacity = '1';
    drawer.style.transition = 'transform 0.2s';
    drawer.style.transform = 'translateY(0)';
  };

  enableDrag = () => {
    const draggable = this.getDraggable();
    if (draggable) {
      draggable.addEventListener('touchstart', this.onTouchStart);
    }
  }
  disableDrag = () => {
    const draggable = this.getDraggable?.();
    if (draggable) {
      draggable.removeEventListener('touchstart', this.onTouchStart);
    }
  }
}